const store = require('../store/index');
const main = {
  power(id, value) { // 权限判断
    // id: 菜单id(1 店铺管理, 2 帐号管理, 3权限管理)
    // value：权限id；1 查看 2 创建 3 编辑 4 开启/禁用
    // arr：有权限的菜单和操作
    let grouplist = store.default.state.userinfo.grouplist;
    let arr = JSON.parse(grouplist);
    // console.log(grouplist);
    let status = true;
    if (arr) {
      for(let i in arr) {
        if(arr[i].id== id){
          if ( $.inArray(value, arr[i].power) != -1) {
            status = true
          } else {
            status = false
          }
          break
        }
      }
    } else {
      status = true
    }
    return status
  }
};
export default main



