import Vue from 'vue'
import Router from 'vue-router'

import AppView from '@/components/app-view'

Vue.use(Router);
const page = name => () => import('@/pages/' + name);
const router =  new Router({
  routes: [
    // {path: '/login', name: 'login', component: page('login')},
    {
      path: '',
      component: AppView,
      children: [
        {path: '/storeIndex', name: 'storeIndex', component: page('store/storeIndex'),meta: {keepAlive: false}},
        {path: '/storeDetails', name: 'storeDetails', component: page('store/storeDetails'),meta: {keepAlive: false}},
        {path: '/storeDetails_vip', name: 'storeDetails_vip', component: page('store/storeDetails_vip'),meta: {keepAlive: false}},
        {path: '/storeAddEdit', name: 'storeAddEdit', component: page('store/storeAddEdit'),meta: {keepAlive: false}},
        {path: '/storeAddEdit_vip', name: 'storeAddEdit_vip', component: page('store/storeAddEdit_vip'),meta: {keepAlive: false}},
        {path: '/addBrand', name: 'addBrand', component: page('store/addBrand'),meta: {keepAlive: false}},
        {path: '/accountIndex', name: 'accountIndex', component: page('account/accountIndex'),meta: {keepAlive: false}},
        {path: '/roleIndex', name: 'roleIndex', component: page('role/roleIndex'),meta: {keepAlive: false}},
      ]
    }

  ]
});

/* 路由跳转前 路由拦截 */
router.beforeEach((to, from, next) => {
  next()
  // if (sessionStorage.getItem('token')) { // 已经登陆
  //   next() // 正常跳转
  // } else {
  //   // window.location.href = 'http://www.baidu.com'
  // }
});

/* 路由跳转后滚动条归0 */
// router.afterEach(() => {
//   window.scrollTo(0, 0)
// })
export default router



