import Vue from 'vue'
import Axios from 'axios'
import router from '../router'
import Vuex from '../store/index.js'
Axios.defaults.baseURL = config.host; // 在index.html中引入的 // Vuex.state.host;
Axios.defaults.timeout = 50000;
Axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
Axios.defaults.headers.post['Accept'] = 'application/json'
Axios.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded'
Axios.defaults.headers.get['Accept'] = 'application/json'
Axios.defaults.headers.delete['Content-Type'] = 'application/x-www-form-urlencoded'
Axios.defaults.headers.delete['Accept'] = 'application/json'
Axios.defaults.headers.put['Content-Type'] = 'application/x-www-form-urlencoded'
Axios.defaults.headers.put['Accept'] = 'application/json'
// status < 500 不会抛错误
// Axios.defaults.validateStatus = status => {
//   return status < 500
// }
// 设置请求token
Axios.interceptors.request.use(config => {
  let token = sessionStorage.getItem('token');
  // console.log(loginInfo)
  // if (token) {
  //   config.headers.get.token = token;
  //   config.headers.post.token = token;
  //   config.headers.delete.token = token;
  //   config.headers.put.token = token
  // }
  // console.log(to)
  config.headers = {
    // 'token': token?token:''
  };
  // console.log(config)
  return config
});

// 接口错误拦截
Axios.interceptors.response.use(res => {
  // console.log(res)
  // if (res.status === 401) {
  //   app && app.$message({
  //     type: 'warning',
  //     message: '登录身份过期，请重新登录。'
  //   })
  //   sessionStorage.removeItem('token');
  //   router.push({name: 'login'})
  //   // return Promise.reject(new Error('身份过期'))
  // } else {
  //   return res
  // }
  return res.data
}, err => {
  console.log(err);
  app.$notify.error({
    title: '服务错误',
    message: '服务器响应错误 ' + err.message
  });
  return Promise.reject(err)
});
var app = new Vue({});
export default Axios



