<template>
  <div class="app-page-body">
    <el-container class="app-container" v-if="userinfo">
      <el-header height="auto" style="padding: 0;">
        <Header :userinfo="userinfo"></Header>
      </el-header>
      <el-container style="height: calc(100% - 100px)">
        <el-aside width="242px" class="app-aside-box">
          <div class="shadow"></div>
          <Aside :userinfo="userinfo"></Aside>
        </el-aside>
        <el-main class="el-main" id="scrollMain">
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
  import Header from './app-header.vue'
  import Aside from './app-aside.vue'
  import { GetLoginInfo } from '@/axios/api'
  import { mapState } from 'vuex'
  export default {
    name: 'app-page-body',
    data() {
      return {
        userinfo: null
      }
    },
    computed: {
      ...mapState(["host"])
    },
    watch: {
      // 监听路由变化，置顶，el-main
      $route(to, form) {
        $("#scrollMain").scrollTop(0)
      }
    },
    components: {
      Header,
      Aside
    },
    created() {
      // 存token types
      let query = this.$route.query;
      let token = query.token;
      if (token) {
        sessionStorage.setItem('token', token);
        this.getLoginInfo(token)
      } else {
        this.getLoginInfo(sessionStorage.getItem('token'))
      }
      // sessionStorage.setItem('token', '7815f3bfa86423a2a2fb9f2ec65fa6b06d3170f1');
      // sessionStorage.setItem('types', '2'); // 当前账号店铺类型 1普通店铺 2会员店铺(目前当前来判断添加店铺类型)
    },
    mounted() {

    },
    methods: {
      getLoginInfo(token) {
        GetLoginInfo({
          token: token
        }).then(res => {
          if (res.status == 1) {
            this.userinfo = res.data;
            this.$store.state.userinfo = res.data;
          } else {
            this.$message.error(res.msg)
          }
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  .app-page-body{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0 auto;
    min-width: 1200px;
    overflow-x: auto;
  }
  .app-container{
    height: 100%;
    overflow: hidden;
  }
  .el-main{
    height: 100%;
    min-height: 100%;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 30px;
  }
  .shadow{
    width: 100%;
    height: 0;
    box-shadow:0 2px 20px 2px rgba(51,51,51,0.1);
  }
  .app-aside-box{
    box-shadow:0 2px 10px 0 rgba(51,51,51,0.2);
    background: #fff;
  }
</style>



