<template>
  <div id="App">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive">
        <!-- 这里是会被缓存的视图组件 -->
      </router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive">
      <!-- 这里是不被缓存的视图组件 -->
    </router-view>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
  #App {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #333; /* 默认字体颜色*/
    font-size: 16px; /*默认字体大小*/
    width: 100%;
    min-width: 1200px;
    overflow: auto;
  }
  body{
    margin: 0;
    padding: 0;
    background: #F9F9F9;
  }
</style>



