<template>
  <div class="app-header">
    <div class="header-box box-sizing flex-sb">
        <div class="header-left flex-row">
          <img src="../assets/logo.png" class="logo">
          <span class="admin-name">商家平台</span>
        </div>
        <div class="header-right flex-row">
          <img :src="host+userinfo.cover" class="user-ava" v-if="userinfo.cover">
          <img src="../assets/default.jpg" class="user-ava" v-else>
          <span class="user-name hover pullDown">{{userinfo.store_name}}</span>
          <span class="icon pullDown" id="iconRotate"><i class="el-icon-caret-bottom"></i></span>
        </div>
        <!-- -->
        <div class="pull-down" id="pullDownBox">
          <ul class="box-ul">
            <li @click="editPsdLayerShow">修改密码</li>
            <li @click="loginOut">退出登录</li>
          </ul>
        </div>
    </div>
    <!-- 修改密码 -->
    <div class="layer-box-page " id="editPasswordLayer">
      <div class="scroll-box">
        <div class="layer-content-page" style="width: 800px;">
          <div class="layer-title-box">
            <div class="title">修改密码</div>
            <img src="../assets/layer_close_icon.png" class="layer-close-icon hover" @click="closeLayer">
          </div>
          <div class="layer-content box-sizing">
            <div class="content">
              <div class="input-box box-sizing flex-row">
                <img src="../assets/ic_mima2x.png" class="ipt-icon">
                <input :type="isOld?'text':'password'" v-model="old_password" placeholder="请输入原密码" class="ipt box-sizing" autocomplete="off">
                <img :src="isOld?require('../assets/ic_zhengyan2x.png'):require('../assets/close_psd_icon.png')" class="see-icon hover" style="width: 32px;" @click="seeOldPsd">
              </div>
              <div class="input-box box-sizing flex-row">
                <img src="../assets/ic_xinmima.png" class="ipt-icon">
                <input :type="isNew?'text':'password'" v-model="new_password"  placeholder="请输入新密码" class="ipt box-sizing" autocomplete="off">
                <img :src="isNew?require('../assets/ic_zhengyan2x.png'):require('../assets/close_psd_icon.png')" class="see-icon hover"  @click="seeNewPsd">
              </div>
              <div class="btn-sub hover" @click="onSubmitData">提交</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { ModifyPassword } from '@/axios/api'
  import { mapState } from 'vuex'
  export default {
    name: 'app-header',
    data() {
      return {
        isOld: false,
        isNew: false,
        old_password: '',
        new_password: ''
      }
    },
    props: ['userinfo'],
    computed: {
      ...mapState(["host"])
    },
    components: {},
    mounted() {
      this.pullDownUp()
    },
    methods: {
      seeOldPsd() {
        this.isOld = !this.isOld
      },
      seeNewPsd() {
        this.isNew = !this.isNew
      },
      pullDownUp() {
        $(".pullDown").click(function(){
          if ($("#iconRotate").hasClass('up')) {
            $("#iconRotate").removeClass('up')
          } else {
            $("#iconRotate").addClass('up');
          }
          $("#pullDownBox").slideToggle(100)
        })
      },
      editPsdLayerShow() {
        $("#editPasswordLayer").fadeIn(100);
        $("#pullDown").click()
      },
      closeLayer() {
        $("#editPasswordLayer").fadeOut(100);
      },
      // 退出登录
      loginOut() {
        sessionStorage.clear();
        // this.changePage('/login', {})
        window.location.href = config.host+'/pt/enterExplain.html'
      },
      // 修改密码
      onSubmitData() {
        if (!this.old_password) {
          this.$message.error('请输入原密码');
          return
        }
        if (!this.new_password) {
          this.$message.error('请输入新密码');
          return
        }
        ModifyPassword({
          token: sessionStorage.getItem('token'),
          old_password: this.old_password,
          new_password: this.new_password
        }).then(res => {
          if(res.status == 1) {
            this.$message.success('修改成功');
            sessionStorage.clear();
            setTimeout(function(){
              // this.changePage('/login', {})
              window.location.href = config.host+'/pt/enterExplain.html'
            }, 1500)
          } else {
            this.$message.error(res.msg)
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>

</style>



