<template>
  <div class="app-aside">
    <el-menu :default-openeds="openeds" :default-active="navActive?navActive:'1-0'">
      <template v-for="(item, i) in menus">
        <el-menu-item :index="item.id" v-if="!item.submenu" style="color: #666;">
          <div class="flex-row">
            <img :src="item.icon" class="el-icon-img">{{item.label}}
          </div>
        </el-menu-item>
        <el-submenu :index="item.id" v-else>
          <template slot="title">
            <div :class="['flex-row', (i+1)==openeds[0]?'active':'']">
              <img :src="item.activeIcon" class="el-icon-img" v-if="(i+1)==openeds[0]">
              <img :src="item.icon" class="el-icon-img" v-else>{{item.label}}
            </div>
          </template>
          <el-menu-item-group>
            <el-menu-item class="line-active" style="padding-left: 65px;" :index="item.id+'-'+sub.id" v-for="(sub, j) in item.submenu" :key="j" @click="changePage(''+sub.path+'', {navActive: ''+(i+1)+'-'+j+''})">{{sub.label}}</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
      </template>


    </el-menu>
  </div>
</template>
<script>
  import { } from '@/axios/api'
  import { mapState } from 'vuex'
  export default {
    name: 'app-aside',
    data() {
      return {
        openeds: [],
        menus: [
          // {
          //   id: '0',
          //   label: '首页', // 记得改上面的跳转时的 i 别加一
          //   icon: require('../assets/ic_shouye.png'),
          //   path: ''
          // },
          {
            id: '1',
            label: '店铺管理',
            icon: require('../assets/ic_dianpu1.png'),
            activeIcon: require('../assets/ic_dianpu2.png'),
            submenu: [
              {id: '0', label: '店铺列表', path: '/storeIndex'},
              // {id: '1', label: '创建店铺', path: '/storeAddEdit'}
            ]
          },
          {
            id: '2',
            label: '帐号管理',
            icon: require('../assets/ic_zhanghao1.png'),
            activeIcon: require('../assets/ic_zhanghao2.png'),
            submenu: [
              {id: '0', label: '帐号列表', path: '/accountIndex'}
            ]
          },
          {
            id: '3',
            label: '权限管理',
            icon: require('../assets/ic_quanxian1.png'),
            activeIcon: require('../assets/ic_quanxian2.png'),
            submenu: [
              {id: '0', label: '角色列表', path: '/roleIndex'}
            ]
          }
        ]
      }
    },
    props: ['userinfo'],
    computed: {
      ...mapState(["host", "navActive"])
    },
    components: {},
    watch: {
      navActive: function(val) { // 必须是普通函数写法
        if(val) {
          let arr = val.split('-');
          this.openeds = [arr[0]]
        }
      }
    },
    created() {
      let rolues = this.userinfo.grouplist;
      if (rolues) {
        rolues = JSON.parse(this.userinfo.grouplist);
        let menus = this.menus;
        for(let i in menus) {
          if (i !=0 ) {
            // 从第二个开始
            for(let j in rolues) {
              if (menus[i].id == rolues[j].id) {
                break
              } else {
                if (j >= rolues.length-1) { // 到了rolues 最后一项
                  if(menus[i].id != rolues[j].id){ //最后一项还没相等 就删除当前菜单
                    menus.splice(i, 1);
                  }
                }
              }
            }
          }
        }
        this.menus = menus;
      } else {
        // 显示所有菜单
      }
    },
    mounted() {
      $(".el-submenu__title").css({
        'font-size': '18px',
        'color': '#666'
      });
    },
    methods: {

    }
  }
</script>
<style lang="less" scoped>
  .el-menu-item{
    color: #999;
    /*font-size: 18px;*/
  }
  .el-menu-item.is-active{
    background: #F9F9F9;
    color: #333;
    position: relative;
  }
  .el-menu-item.line-active.is-active:after{
    content: '';
    width: 2px;
    height: 13px;
    background: #EE5567;
    position: absolute;
    left: 52px;
    top: 19px;
  }
  .el-submenu.is-opened.is-active .el-submenu__title{
    color: #333 !important;
  }
  .el-submenu__title{
    color: #666 !important;
    /*font-size: 18px !important;*/
  }
  .el-icon-img{
    width: 20px;
    margin-right: 20px;
  }
  .active{
    color: #333;
  }
</style>



